.sba-logo {
    align-items: center;
    display: flex;
    flex-flow: row nowrap;
    font-size: 1rem;
    line-height: 0;

    a {
        display: flex;
        align-items: center;
    }

    &__image {
        margin: 0 ($grid-columngap * 2) 0 0;
    }

    &__text {
        font-size: .875em; /* 14px */
        font-weight: 700;
        text-transform: uppercase;
    }
}

.sba-enableCookie {
    padding-top: 54px;
    @media (max-width: $media-max-M) {
        padding-top: 0;
    }
}

.sba-cookies {
    min-height: 34px;
    background-color: $prussian-blue;
    padding: 10px 0;
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9999;
    color: $rock-blue;
    align-items: center;

    .sba-enableCookie & {
        display: flex;
    }

    @media (max-width: $media-max-M) {
        position: relative;
        padding: 10px 6px;
    }

    &__inner {
        margin: 0 auto;
        width: 100%;
        max-width: $base-maxwidth;
        padding: 0 ($grid-columngap / 2);
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 14px;
        line-height: 1.5;
    }

    a {
        color: white;
    }
}

.sba-header {
    color: #fff;
    font-size: 1rem;
    /*position: relative;*/
}

.sba-header__inner {
    align-items: center;
    display: flex;
    flex-flow: row nowrap;
    margin: 0 auto;
    max-width: $base-maxwidth;
    padding: 0 ($grid-columngap / 2);
    position: relative;

    &::after {
        background-color: currentColor;
        content: "";
        display: block;
        height: 1px;
        left: ($grid-columngap / 2);
        position: absolute;
        right: ($grid-columngap / 2);
        top: 100%;
    }

    @media (max-width: $media-max-XL) {
        padding: 0 $grid-outer-mobile-padding;
    }
}

.sba-header__search {
    background: none;
    color: inherit;
    cursor: pointer;
    border: 0;
    margin: 0 0 0 (1.5 * $grid-columngap);
    padding: 0;
}

.sba-header {
    position: sticky;
    top: 0;
    z-index: 12;

    &::before {
        background-color: $color-blue;
        bottom: 0;
        content: "";
        left: 0;
        opacity: 0;
        position: absolute;
        right: 0;
        top: 0;
        transition: opacity 250ms ease-out;
        z-index: -1;
    }


    &.js-nohero::before {
        opacity: 1;
        transition: none;
    }

    .js-nav &:before,
    &.js-sticky::before {
        opacity: 1;
    }
}

.sba-header__inner {
    min-height: $base-lineheight; /* 30px */
    padding-bottom: 25px;
    padding-top: 20px;
}

.sba-header__navigation-link--store {
    background-color: #fff;
    color: $color-blue;
    padding: 0 12px;
}


@media (min-width: 1153px) {

    .sba-corpobar {
        background-color: #fff;
        color: $color-blue;
        font-size: 1rem;
    }
    .sba-corpobar__inner {
        margin: 0 auto;
        max-width: $base-maxwidth;
        padding: 0 ($grid-columngap / 2);
    }
    .sba-corpobar__navigation-list {
        align-items: center;
        display: flex;
        justify-content: flex-end;
        margin: 0 auto;
        max-width: $base-maxwidth;
        padding: .625em ($grid-columngap / 2); /* 10px 0 */
    }
    .sba-corpobar__navigation-item {
        margin: 0 0 0 $grid-columngap;
        position: relative;
    }
    .sba-corpobar__navigation-link {
        display: inline-block;
        font-size: .75em; /* 12px */
        line-height: 2.5em; /* 30px */
        white-space: nowrap;
    }
    .sba-corpobar__navigation-link svg {
        vertical-align: middle;
    }
    .sba-corpobar__navigation-link svg:first-child {
        margin: 0 4px 0 0;
    }
    .sba-corpobar__navigation-link svg:last-child {
        margin: 0 0 0 4px;
    }


    .sba-corpobar__navigation-sublist {
        background-color: #03235C;
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .5);
        color: #fff;
        left: 50%;
        min-width: 100%;
        opacity: 0;
        padding: .46875em 9px .234375em;
        pointer-events: none;
        position: absolute;
        top: 100%;
        transform: translateX(-50%) translateX(4px) scale(0);
        transform-origin: 60% 0%;
        transition: transform .25s ease-out, opacity .25s ease-out;
        z-index: 13;

        &::before {
            border-style: solid;
            border-width: 0 5px 4px 5px;
            border-color: transparent transparent #03235C transparent;
            content: "";
            height: 0;
            left: 60%;
            position: absolute;
            top: -4px;
            width: 0;
        }
    }
    .sba-corpobar__navigation-subitem {
        min-width: 90px;
    }
    .sba-corpobar__navigation-subitem:not(:last-child) {
        border-bottom: 1px solid rgba(216, 216, 216, .5)
    }
    .sba-corpobar__navigation-sublink {
        display: inline-block;
        font-size: .75em; /* 12px */
        line-height: 2.5em; /* 30px */
        white-space: nowrap;
    }


    .sba-corpobar__navigation-item.js-hover .sba-corpobar__navigation-sublist {
        opacity: 1;
        pointer-events: auto;
        transform: translateX(-50%) translateX(4px) scale(1);
    }

    .sba-header__burger {
        display: none;
    }

    .sba-header__content {
        margin: 0 0 0 auto;
    }

    .sba-header__navigation {
    }
    .sba-header__navigation-list {
        display: flex;
        flex-flow: row nowrap;
        margin: 0 (-1.25 * $grid-columngap);
    }
    .sba-header__navigation-item {
        padding: 0 (1.25 * $grid-columngap);
    }
    .sba-header__navigation-link {
        display: block;
        font-size: .875em; /* 14px */
        font-weight: 700;
        line-height: 2.143em; /* 30px */
    }
    .sba-header__navigation-link svg {
        vertical-align: middle;
    }

    .sba-header__navigation-item--store {
        padding-left: (1.75 * $grid-columngap);
    }


}

@media (max-width: 1152px) {
    /*
    .sba-Header {
      background-color: $color-blue;
    }
    */

    .sba-header__logo {
        margin: 0 auto 0 0;
    }

    /**
     * Bouton "hamburger"
     * ===
     */
    .sba-header__burger {
        background: none;
        border: 0;
        color: inherit;
        cursor: pointer;
        font: 0/0 a;
        margin: 0 0 0 (1.5 * $grid-columngap);
        order: 1;
        overflow: hidden;
        padding: 0;
        width: 24px;
    }
    .sba-header__burger span,
    .sba-header__burger span::after,
    .sba-header__burger span::before {
        background-color: currentColor;
        border-radius: 0;
        display: block;
        height: 2px;
        width: 100%;
    }
    .sba-header__burger span {
        margin: 8px 0;
        position: relative;
        transform: translateY(0px);
        transition: all 0ms 250ms;
    }
    .sba-header__burger span::before,
    .sba-header__burger span::after {
        content: "";
        position: absolute;
        left: 0;
    }
    .sba-header__burger span::before {
        bottom: 6px;
        transition: bottom 250ms ease-out 250ms, transform 250ms ease-out 0ms;
    }
    .sba-header__burger span::after {
        top: 6px;
        transition: top 250ms ease-out 250ms, transform 250ms ease-out 0ms;
    }

    .sba-header__content {
        bottom: 0;
        box-sizing: border-box;
        left: 0;
        opacity: 0;
        padding: 0 6px;
        pointer-events: none;
        position: fixed;
        top: 135px;
        width: 100%;
        z-index: 12;
    }
    .sba-header__content::before {
        background-color: $color-blue;
        bottom: 0;
        content: "";
        left: 0;
        opacity: 1;
        position: absolute;
        right: 0;
        top: 0;
        z-index: -1;
    }

    .sba-header__navigation {
        box-sizing: border-box;
        height: 100%;
        overflow-y: auto;
        padding: 0 0 1.875em;
    }
    .sba-header__navigation-list:not(:last-child) {
        border-bottom: 1px solid #d8d8d8;
        margin: 0 0 1.875em;
        padding: 0 0 1.875em;
    }
    .sba-header__navigation-item:not(:last-child) {
        margin: 0 0 .9375em;
    }
    .sba-header__navigation-item--store {
        border-top: 1px solid #d8d8d8;
        margin: 1.875em 0 0;
        padding: 1.875em 0 0;
    }
    .sba-header__navigation-link {
        display: inline-block;
        font-size: 1.125em; /* 18px */
        font-weight: 700;
        line-height: 1.667em; /* 30px */
    }

    .sba-header__navigation-link svg {
        display: none;
    }

    .js-nav--animate .sba-header__content {
        transition: opacity 250ms ease-out;
    }
    .js-nav {
        overflow: hidden;
    }
    .js-nav .sba-header__content {
        opacity: 1;
        pointer-events: auto;
    }
    .js-nav .sba-header__inner {
        background: none;
    }


    .js-nav .sba-header__burger span {
        background: rgba(255, 255, 255, 0);
    }
    .js-nav .sba-header__burger span::after {
        top: 0;
        transform: rotate(45deg);
        transition: top 250ms ease-out 0ms, transform 250ms ease-out 250ms;
    }
    .js-nav .sba-header__burger span::before {
        bottom: 0;
        transform: rotate(-45deg);
        transition: bottom 250ms ease-out 0ms, transform 250ms ease-out 250ms;
    }


}


