$elem: sba-backtotop;

.#{$elem} {
  background-color: #f0f2f9;

  &__inner {
    align-items: center;
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-end;
    margin: 0 auto;
    max-width: $base-maxwidth;
    padding: .625em ($grid-columngap / 2) /* 10px 0 */;
    pointer-events: none;

    @media (max-width: 384px) {
      justify-content: center;
      pointer-events: auto;
    }
  }

  &__icon,
  &__text {
    pointer-events: auto;
  }

  &__icon {
    align-items: center;
    display: inline-flex;
    justify-content: center;
    width: ($grid-columngap * 2);
  }

  &__text {
    font-size: .875em; /* 14px */
    font-weight: 700;
    line-height: 2.143em; /* 30px */
  }
}



