$elem: sba-videos;

.#{$elem} {
    font-size: 1rem;
    padding: 2.8125em 0 8.4375em; /* 45px 0 135px */

    &__inner {
        margin: 0 auto;
        max-width: $base-maxwidth;
        padding: 0 ($grid-columngap / 2);
    }


    &__banner {
        align-items: center;
        background-color: #fbfbfb;
        display: flex;
        flex-flow: row nowrap;
        margin: 0 0 1.875em; /* 0 0 30px */
        padding: 2.34375em 24px; /* 37.5px 24px */

        &-icon {
            margin: 0 30px 0 0;
        }

        &-name {
            font-size: 1.125em; /* 18px */
            font-weight: 700;
            line-height: 1.111em; /* 20px */
        }

        &-description {
            font-size: .875em; /* 14px */
            line-height: 1.429em; /* 20px */
            margin: 0 0 .714em; /* 0 0 10px */
        }

        &-button {
            line-height: 0;
        }
    }
}


.sba-video {
    font-size: 1rem;
    margin: 0 auto;
    max-width: 372px;

    &__image {
        position: relative;
        display: block;
        &::before {
            content: "";
            display: block;
            padding: 56.25% 0 0;
        }

        &::after {
            background: #fff url("data:image/svg+xml,%3Csvg height='16' width='16' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15 8L3 14V2l12 6zm0 0' fill='%2306205b' fill-rule='evenodd'/%3E%3C/svg%3E") 50% / 50% 50% no-repeat;
            border-radius: 50%;
            content: "";
            height: 60px;
            left: 50%;
            opacity: .7;
            position: absolute;
            top: 50%;
            transform: translate(-50%, -50%);
            width: 60px;
            z-index: 1;
        }

        img {
            height: 100%;
            left: 0;
            object-fit: cover;
            position: absolute;
            top: 0;
            width: 100%;
        }
    }

    &__inner {
        font-size: .875em; /* 14px */
        line-height: 1.429em; /* 20px */
        padding: .9375em 12px 1.875em; /* 15px 12px 30px */
    }

    &__name,
    &__date {
        color: $color-blue;
    }

    &__description {
        max-height: 4.5em;
    }

}
