$elem: sba-hero;

.#{$elem} {
    color: #fff;
    font-size: 1rem;
    position: relative;
    z-index: -1;
    display: flex;

    &::after {
        background-color: $color-blue;
        bottom: 0;
        content: "";
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
        z-index: 2;
    }

    &__inner {
        box-sizing: border-box;
        display: flex;
        flex-flow: column;
        margin: 0 auto;
        max-width: calc(#{$base-maxwidth} + #{$grid-columngap});
        overflow: hidden;
        padding: 0 ($grid-columngap / 2) 1.875em;
        position: relative;
        z-index: 3;
        width: 100%;
        @media (max-width: $media-max-XL) {
            padding-left: $grid-outer-mobile-padding;
            padding-right: $grid-outer-mobile-padding;
        }
    }

    &__title {
        font-size: 1em;
        /*margin: 4.6875em 0 0; /* 75px 0 0 */
        margin: auto 0;
    }

    /*b*/
    &__name {
        display: inline-block;
        font-weight: 700;
        line-height: 1;
    }

    /*span*/
    &__baseline {
        display: inline-block;
        font-weight: 400;
        line-height: 1;
    }

    &__content {
        width: 66.667%;

        p:not(:last-child) {
            margin: 0 0 .9375em;
        }

        strong {
            display: block;
            line-height: 1.125em; /* 45px */
        }

        @media (max-width: 864px) {
            width: 100%;
        }
    }

    &__scroll {
        align-items: center;
        border-radius: 20px;
        border: 1px solid;
        content: "";
        display: flex;
        flex-flow: column;
        height: 70px;
        justify-content: center;
        margin: auto auto 28px;
        position: relative;
        width: 40px;

        &::before {
            animation: mouse-scroll 2s linear infinite;
            background-color: currentColor;
            border-radius: 50%;
            content: "";
            height: 7px;
            left: 16.5px;
            position: absolute;
            transform: translateX(-50%);
            top: 10px;
            width: 7px;
        }

        &::after {
            content: "SCROLL";
            display: block;
            line-height: 1;
            margin: auto 0 0;
            padding: 13px 0 0;
            transform: translateY(100%);
        }
    }


    &--home,
    &--cluster {
        .#{$elem}__title {
            padding: 1.875em 0;
            margin: 10vh 0;
        }

        @media (min-width: 1153px) {

            .#{$elem}__name {
                font-size: 7.5em; /* 120px */
            }
            .#{$elem}__baseline,
            .#{$elem}__content strong {
                font-size: 2.5em; /* 40px */
            }

        }
        @media (max-width: 1152px) and (min-width: 385px) {

            .#{$elem}__name {
                font-size: calc(3.75em + (7.5 - 3.75) * ((100vw - 24.0625rem) / (72 - 24.0625)));
            }
            .#{$elem}__baseline,
            .#{$elem}__content strong {
                font-size: calc(1.5625em + (2.5 - 1.5625) * ((100vw - 24.0625rem) / (72 - 24.0625)));
            }

        }
        @media (max-width: 384px) {

            .#{$elem}__name {
                font-size: 3.3125em; /* 53px */
            }

            .#{$elem}__baseline,
            .#{$elem}__content strong {
                font-size: 1.5625em; /* 25px */
            }

        }
    }

    &--home {

        .#{$elem}__content {
            p:not(:first-child) {
                width: calc(100% / 12 * 8);

                @media (max-width: 864px) {
                    width: 100%;
                }
            }

            @media (max-width: 864px) {
                margin-bottom: 1.875em;
            }
        }
    }

    /**
     * Modifier pour les pages voulant afficher un media tout en laissant
     * apparaitre le contenu suivant
     * ===
     *
     */
    &--media,
    &--media-cover {
        .#{$elem}__inner {
            @media (min-width: 1153px) {
                min-height: calc(100vh - 50px - 90px);
            }
            @media (max-width: 1152px) {
                min-height: calc(100vh - 90px);
            }
        }


        &::after {
            opacity: .6;
        }

        .#{$elem}__media {
            background: #000 50% / cover no-repeat;
            bottom: 0;
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
            overflow: hidden;

            iframe {
                height: 100%;
                width: 100%;
            }

            video {
                bottom: -10000px;
                height: auto;
                left: -10000px;
                margin: auto;
                position: absolute;
                right: -10000px;
                top: -10000px;
                width: 100%;
            }

            @supports (object-fit: cover) {
                video {
                    bottom: auto;
                    height: 100%;
                    left: 0;
                    object-fit: cover;
                    right: auto;
                    top: 0;
                }
            }
        }
    }


    /**
     * Modifier pour les pages voulant couvrir la première vue
     * ===
     *
     */
    &--media-cover {
        .#{$elem}__inner {
            @media (min-width: 1153px) {
                min-height: calc(100vh - 50px);
            }
            @media (max-width: 1152px) {
                min-height: 100vh;
            }
        }
    }


    /**
     * Modifier pour les pages wysiwyg
     * ===
     *
     * Titre + Baseline générique sur fond bleu.
     */
    &--page {
        .#{$elem}__title {
            margin: auto 0 0;
            padding-top: 3.75em; /* 60px */
            padding-bottom: 1.25em; /* 20px */
        }

        .#{$elem}__name {
            line-height: 1.167em;
            margin-bottom: .333em; /* 30px */
        }

        .#{$elem}__baseline {
            font-weight: 300;
            line-height: 1.2em;
        }

        @media (min-width: 1153px) {

            .#{$elem}__name {
                font-size: 5.625em; /* 90px */
            }
            .#{$elem}__baseline {
                font-size: 3.125em; /* 50px */
            }

        }
        @media (max-width: 1152px) and (min-width: 385px) {

            .#{$elem}__name {
                font-size: calc(2.8125em + (5.625 - 2.8125) * ((100vw - 24.0625rem) / (72 - 24.0625)));
            }
            .#{$elem}__baseline {
                font-size: calc(1.5625em + (2.5 - 1.5625) * ((100vw - 24.0625rem) / (72 - 24.0625)));
            }

        }
        @media (max-width: 384px) {

            .#{$elem}__name {
                font-size: 2.5em; /* 40px */
            }
            .#{$elem}__baseline {
                font-size: 1.5625em; /* 25px */
            }
        }

        &.#{$elem}--media::after {
            background: linear-gradient(to bottom, rgba(6, 32, 91, 0), rgba(6, 32, 91, .5)) 50% / 100% 100% no-repeat;
        }

    }

}


/**
 * Animation de la souris "SCROLL"
 * ===
 *
 * @see: https://dribbble.com/shots/2807757-Micro-animation-for-site-scroll-icons
 */
@keyframes mouse-scroll {
    0% {
        transform: translateY(100%) scale(0);
        opacity: 0;
    }
    33% {
        transform: translateY(0%) scale(1);
        opacity: 1;
    }
    66% {
        transform: translateY(300%) scale(.7);
        opacity: 1;
    }
    100% {
        transform: translateY(550%) scale(.3);
        opacity: .2;
    }
}
