$elem: sba-popin;

.#{$elem} {
  align-items: center;
  box-sizing: border-box;
  display: flex;
  flex-flow: row nowrap;
  height: 100%;
  justify-content: center;
  left: 0;
  font-size: 1rem;
  padding: 6px;
  pointer-events: none;
  position: fixed;
  top: 0;
  visibility: hidden;
  width: 100%;
  z-index: 21;

  &::before {
    background-color: $base-color;
    content: "";
    height: 100%;
    left: 0;
    opacity: .7;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: -1;
  }

  &__inner {
    background-color: #fff;
    box-sizing: border-box;
    max-width: 100%;
    padding: 1.875em 24px; /* 30px 24px */
    pointer-events: auto;
    position: relative;
  }

  &.js-show {
    pointer-events: auto;
    visibility: visible;
  }
}

.#{$elem}--video {


  &::before {
    opacity: 0;
    transition: opacity .25s ease-out;
  }

  .#{$elem}__inner {
    background-color: transparent;
    padding: 0;
  }

  .#{$elem}__close {
    background: none;
    border: 0;
    bottom: calc(100% - 8px);
    cursor: pointer;
    font: 0/0 a;
    height: 40px;
    padding: 0;
    position: absolute;
    right: -32px;
    width: 40px;

    &::before,
    &::after {
      background-color: #fff;
      content: "";
      height: 2px;
      left: 50%;
      margin: 0 0 0 -12px;
      position: absolute;
      transition: transform .125s ease-out;
      visibility: visible;
      width: 24px;
    }
    &::after { transform: rotate(45deg) scale(0); }
    &::before { transform: rotate(-45deg) scale(0); }
  }

  .#{$elem}__body {
    transform-origin: 0 0;
  }
  .#{$elem}__body.js-move {
    transform: translate(0, 0) scale(1, 1) !important;
    transition: transform .625s ease-out;
  }
  .#{$elem}__body.js-reduce {
    transform: translate(0, 0) scale(1, 1);
    transition: transform .625s ease-out;
  }



  &.js-overlay::before {
    opacity: .7;
  }


}

.js-popin .#{$elem}--video .#{$elem}__close::before,
.js-popin .#{$elem}--video .#{$elem}__close::after {
  transition: transform .25s ease-out .625s;
}

.js-popin .#{$elem}--video .#{$elem}__close::before {
  transform: rotate(-45deg) scale(1);
}
.js-popin .#{$elem}--video .#{$elem}__close::after {
  transform: rotate(45deg) scale(1);
}

.sba-site {
  //transition: filter .25s ease-out;
}

/*html*/.js-freeze {
    overflow: hidden;
    pointer-events: none;
}
/*html*/.js-popin .sba-site {
  filter: blur(7px);
}
