.sba-button {
    background: none;
    border: 1px solid;
    box-sizing: border-box;
    color: #fff;
    display: inline-flex;
  font: 400 1rem/1.875 Roboto, sans-serif;
    margin: 0;
    padding: 0;
    position: relative;
    text-align: center;
    text-decoration: none;
  text-transform: uppercase;
    user-select: none;
}

.sba-button::-moz-focus-inner {
    border: 0;
    padding: 0;
}

.sba-button:focus {
    outline: 1px dotted;
    outline: 5px auto -webkit-focus-ring-color;
}

.sba-button--small {
    font-size: .875rem; /* 14px */
    font-weight: 700;
    line-height: 1.607em; /* 22.5px */
    padding: .804em 36px; /* 11.25px 36px */
    text-transform: none;
}

.sba-btn {
    padding: 8px 22px;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    transition: ease-in 0.1s;
    outline: 0;

    &--primary {
        border: 1px solid $prussian-blue;
        color: $prussian-blue;

        &:hover {
            color: white;
            background-color: $prussian-blue;
        }

        &--block {
            width: 100%;
        }
    }

    &--secondary {
        color: white;
        background-color: $prussian-blue;
        border: 1px solid transparent;

        &:hover {
            background-color: transparent;
            border: 1px solid $prussian-blue;
            color: $prussian-blue;
        }

        &--block {
            width: 100%;
        }
    }
}

.sba-aircraft-flag {
    font-size: 12px;
    font-weight: 400;
    color: $blue-bayoux;
    border: 1px solid $blue-bayoux;
    border-radius: 30px;
    padding: 7px 15px 6px 15px;
    margin-right: 10px;
}

.sba-category-flag {
    font-size: 12px;
    font-weight: 500;
    color: $prussian-blue;
    background-color: white;
    text-transform: capitalize;
    padding: 7px 15px 6px 15px;
    border: 1px solid white;
    border-radius: 30px;
    margin-right: 10px;
}

.sba-accordion__header {
    display: block;
    background-color: white;
    color: $prussian-blue;
    font-size: 24px;
    font-weight: 500;
    position: relative;
    cursor: pointer;
    border-color: transparent;
    border-top: 1px solid $rock-blue;
    transition: all 0.2s;
    width: 100%;
    text-align: left;
    outline: 0;
    height: 70px;
}

.sba-accordion__header:hover {
    padding-left: 30px;
}

/* title opened */
.sba-accordion__header[aria-expanded="true"] {
    background-color: $prussian-blue;
    padding-left: 30px;
    color: #fff;
    transition: all 0.8s;
}

/* title closed */
.sba-accordion__header[aria-expanded="false"]:before {

}

/* title selected */
.sba-accordion__header[aria-selected="true"]:after {

}

/* title non selected */
.sba-accordion__header[aria-selected="false"]:after {

}

.sba-accordion__panel {
    display: flex;
    max-height: 9999px;
    transition-timing-function: cubic-bezier(0.5, 0, 1, 0);
    transition-delay: 0s;
    overflow: hidden;
}

.sba-accordion__panel[aria-hidden=true] {
    max-height: 0;
    transition: max-height 0.8s cubic-bezier(0,1,0,1)-0.1s;
    overflow: hidden;
}

