.img-contained {
    object-fit: contain;
    font-family: 'object-fit: contain;';
}

.img-covered {
    object-fit: cover;
    font-family: 'object-fit: cover';
}

.sba-services-header {
    position: relative;

    h1 {
        max-width: 83%;
    }

    &__contact-btn {
        position: absolute;
        top: 20px;
        right: 0;
    }

    &__infos {
        display: flex;
        align-items: center;
        font-size: 12px;
        min-height: 70px;
        margin-bottom: 15px;

        & > :not(:last-child) {
            padding-right: 14px;
            border-right: 1px solid $prussian-blue;
        }

        & > :not(:first-child) {
            padding-left: 14px;
        }

        &__supplier {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
        }

        &__supplier-logo {
            display: flex;
            align-items: center;
            padding-left: 8px;
            max-height: 150px;
            width: 200px;

            & img {
                max-width: 100%;
                max-height: 150px;
            }
        }
    }

    &__labels {
        margin-bottom: 25px;

        &__title {
            font-size: 13px;
            margin-right: 10px;
        }

        &__aircraft {
            font-size: 12px;
            font-weight: 500;
            color: $blue-bayoux;
            border: 1px solid $blue-bayoux;
            border-radius: 30px;
            padding: 7px 15px 6px 15px;
            margin-right: 10px;
        }
    }

    &__description {
        margin-bottom: 40px;

        &__text {
            font-size: 16px;
            color: $tundora;
        }
    }
}

.sba-services-accordions {
    &__accordion {
        &__icon {
            pointer-events: none;
            position: absolute;
            right: 20px;
            transition: transform 0.2s ease-in;
            transform: rotate(180deg);

            & svg {
                width: 30px;
                height: 30px;
            }
        }
    }
}

.sba-accordion__header[aria-expanded="true"] .sba-services-accordions {
    &__accordion {
        &__icon {
            pointer-events: none;
            position: absolute;
            right: 20px;
            transform: rotate(0deg);
            transition: transform 0.2s ease-in;
        }
    }
}

.sba-accordion__panel {
    margin: 0 15px;

    .tns-outer:first-of-type {
        border: 1px solid lightgrey;
    }


    &__carousel {
        width: 480px;
        position: relative;
        float: left;
        margin-right: 4vw;
        margin-bottom: 30px;

        &__main {
            width: 480px;
            height: 500px;

            .tns-item {

                transition: ease-out 0.5s;
            }

            .tns-slide-active {
                //opacity: 1;
                //overflow: visible;
                //transform: scaleX(1.05);
                transition: ease-out 2s;
            }

            & > .tns-item > div {
                display: flex;
                justify-content: center;
                align-items: center;
                overflow: hidden;
                height: 500px;

                img.img-contained {
                    object-fit: contain;
                    font-family: 'object-fit: contain;';
                    width: 100%;
                    height: 100%;
                }
            }
        }

        & > .controls {
            display: flex;
            align-items: center;
            position: absolute;
            z-index: 1;
            width: 100%;
            top: 42%;

            & li {
                cursor: pointer;
                position: absolute;
            }

            & li[aria-disabled="true"] {
                display: none;

                &:hover {

                }
            }

            & svg {
                background-color: rgba(156, 156, 156, 0.3);
                color: white;
                padding: 18px 18px;
                width: 14px;
                height: 15px;

                &:hover {
                    background-color: rgba(156, 156, 156, 0.4);
                }

                &:active {
                    background-color: rgba(156, 156, 156, 0.5);
                    color: white;
                }
            }

            & > .prev {
                left: 0;

                & svg {
                    transform: rotate(180deg);
                }
            }

            & > .next {
                right: 0;
            }
        }

        &__thumbnails {
            margin-top: 15px;
            display: flex;
            overflow: hidden;
            align-items: center;

            & li {
                height: 75px;
                overflow: hidden;

                & img {
                    object-fit: cover;
                    width: 100%;
                    height: 100%;
                }
            }

            & .tns-nav-active {
                padding: 3px;

                & > img {
                    outline: 3px solid #0085AD;
                }
            }
        }
    }

    &__content {
        width: 100%;
        padding: 30px 0;

        > h2 {
            font-size: 24px;
            line-height: 1;
            margin-bottom: 30px;
        }

        > p {
            color: $tundora;
            margin-bottom: 15px;
        }

        > ul {
            color: $tundora;
            list-style-type: disc;
            padding-left: 20px;
            overflow: hidden;
        }

        a {
            display: inline-block;
            margin-top: 30px;
            margin-bottom: 30px;
        }
    }
}

.sba-services-benefits {
    background-color: $catskill-white;

    & > .sba-body-inner {
        padding: 1em 20px;

        @media (max-width: $media-max-L) {
            padding-right: $grid-outer-mobile-padding;
            padding-left: $grid-outer-mobile-padding;
        }
    }

    &__title {
        font-size: 24px;
        line-height: 1;
        margin: 20px 0 30px;
    }

    &__list {
        column-count: 2;
        list-style: none;
        margin-left: 0;
        padding-left: 1em;
        text-indent: -1em;

        & li {
            margin-bottom: 1em;

            &:before {
                vertical-align: text-top;
                padding-right: 5px;
                content: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCI+CiAgPHBhdGggZmlsbD0iIzAwMjA1QiIgZD0iTTkgMTYuMkw0LjggMTJsLTEuNCAxLjRMOSAxOSAyMSA3bC0xLjQtMS40TDkgMTYuMnoiLz4KPC9zdmc+");
            }
        }
    }
}

.sba-services-cards-title {
    font-size: 60px;
    line-height: 1;
    margin-bottom: 0.8em;

    & span {
        color: $rock-blue;
    }
}

.sba-services-cards {
    .sba-grid__item {
        a {
            width: 100%;
        }
    }

    &__card {
        height: 100%;
        display: flex;
        flex-direction: column;
        transform: scale(0.97);
        transition: all 0.3s cubic-bezier(.25, .8, .25, 1);

        &__thumbnail {
            position: relative;
            overflow: hidden;

            &:after {
                content: "";
                background: linear-gradient(rgba(255, 255, 255, 0), rgba(6, 32, 91, 1));
                position: absolute;
                display: block;
                width: 100%;
                top: 0;
                bottom: 0;
            }

            & > img {
                display: block;
                border: 0;
                transition: transform 0.3s ease-out;
                width: 100%;
                height: 150px;
                object-fit: cover;
            }

            & > span {
                position: absolute;
                top: 35%;
                color: white;
                font-size: 25px;
                z-index: 1;
                line-height: 1.2;
                padding: 11px;
                max-height: 2.4em;
            }
        }

        &__content {
            padding: 10px 11px 20px;
            border: 1px solid #E0E3E9;
            border-top: transparent;
            display: flex;
            flex-direction: column;
            flex: 1 1 auto;

            &__title {
                font-size: 16px;
                font-weight: 600;
                padding-right: 5px;
                display: inline;
                margin-bottom: 10px;

                & > span {
                    font-size: 13px;
                    font-weight: 400;

                    &:before {
                        content: " - "
                    }
                }
            }

            &__text {
                font-size: 16px;
                color: #06205B;
                line-height: 25px;
                margin-bottom: 24px;
                margin-top: 8px;
                max-height: 5em;
            }

            &__list {
                display: flex;
                flex-wrap: wrap;
                margin-bottom: 5px;

                & li {
                    border: 1px solid #9AA5B8;
                    border-radius: 30px;
                    padding: 8px 13px 7px;
                    color: #9AA5B8;
                    font-size: 12px;
                    margin-right: 4px;
                    margin-bottom: 5px;
                    line-height: 1;
                }
            }

            &__cta {
                display: flex;
                justify-content: flex-end;
                margin-top: auto;

                & > .sba-btn {
                    background: $prussian-blue;
                    border: 1px solid $prussian-blue;
                    font-size: 14px;
                    font-weight: 500;
                    color: #FFFFFF;
                    line-height: 1;
                    width: 100px;
                    padding: 8px 2px 7px;
                }
            }
        }

        &:hover {
            transform: scale(1);
            box-shadow: 5px 20px 30px rgba(0, 0, 0, 0.2);
        }
    }
}

@media screen and (max-width: 900px) {
    .sba-services-benefits__list {
        column-count: 1;
    }
}

@media screen and (max-width: 767px) {

    .sba-services-cards__card__content__list > li {
        padding: 5px 11px 4px;
    }
    .sba-services-cards__card__content__cta > a.sba-btn {
        width: 100%;
    }
    .sba-services-header {
        h1 {
            max-width: 100%;
        }

        &__description {
            margin-bottom: 30px;
        }

        &__contact-btn {
            padding: 8px 0;
            display: inline-block;
            text-align: center;
            position: static;
            width: 100%;
            margin-bottom: 30px;
            border: 1px solid #00205B;
            color: #00205B;
            background-color: #FFF;
        }
    }
    .sba-accordion__panel {
        &__carousel {
            float: none;
            width: auto;
            margin-right: 0;
            margin-bottom: 30px;
        }
    }
    .sba-services-header {
        &__infos {
            flex-direction: column;
            align-items: flex-start;
            height: auto;
            margin-top: 10px;

            & > span, & > div {
                padding: 0 !important;
                border: none !important;
            }

            & span:not(:last-child) {
                margin-bottom: 10px;
            }

            &__supplier {
                display: flex;
                align-items: center;

                & > span {
                    margin-bottom: 0 !important;
                }
            }

            &__supplier-logo {
                max-height: 150px;
                width: auto;
                display: flex;
                align-items: center;

                & img {
                    padding-left: 0;
                }
            }
        }

        &__labels {
            &__title {
                display: block;
            }
        }
    }
}

@media screen and (min-width: 768px) and (max-width: 1052px) {
    .sba-accordion__panel__carousel {
        max-width: 50%;
    }
}

@media screen and (max-width: 1024px) {
    .sba-services-cards__card {
        transform: scale(1);

        &:hover {
            box-shadow: none;
        }
    }
}

