$elem: sba-news;

.#{$elem} {
    font-size: 1rem;
    padding: 1.5625em 0 8.4375em; /* 25px 0 135px */

    @media (max-width: $media-max-L) {
        padding: 1.5625em 0 5em; /* 25px 0 80px */
    }

    &__inner {
        margin: 0 auto;
        max-width: $base-maxwidth;
        padding: 0 ($grid-columngap / 2);

        @media (max-width: $media-max-XL) {
            padding: 0 $grid-outer-mobile-padding;
        }

        &__title {
            @extend .sba-t3;
            color: $prussian-blue;
            margin: 20px 0;
        }

        &__apps {
            padding: 25px;
            background-color: #F9F9F9;
            display: flex;
            align-items: center;
            justify-content: center;
            @media (max-width: $media-max-M) {
                flex-direction: column;
            }

            &__main {
                @media (max-width: $media-max-M) {
                    margin-right: 0;
                    margin-bottom: 20px;
                }
                margin-right: 20px;
            }

            &__side {
                display: flex;
                flex-direction: column;
                @media (max-width: $media-max-M) {
                    flex-flow: column-reverse;
                }

                &__text {
                    margin-bottom: 10px;
                    line-height: 1.2;
                    color: #6C6D6D;
                }

                &__badges {
                    @media (max-width: $media-max-M) {
                        display: flex;
                        justify-content: center;
                        margin-bottom: 10px;
                    }

                    & img {
                        max-height: 35px;
                        width: auto;
                        margin-right: 5px;

                    }
                }
            }
        }

        &__carousel {
            display: flex;
            flex-flow: row-reverse;

            &__main {
                order: 1;
                height: 500px;
                width: 100%;
                @media (max-width: $media-max-XL) {
                    height: 400px;
                }
                @media (max-width: $media-max-L) {
                    height: 300px;
                }

                img {
                    width: 100%;
                    height: 100%;
                }
            }

            &__side {
                display: flex;
                flex-direction: column;
                height: 500px;
                @media (max-width: $media-max-XL) {
                    height: 600px;
                }
                @media (max-width: $media-max-L) {

                }

                &__img {
                    display: flex;
                    flex: 1 1 auto;
                    max-height: 50%;
                    overflow: hidden;

                    &:first-of-type {
                        padding-bottom: 2%;
                    }

                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }
    }
}

.sba-news-slider {
    position: relative;
    margin-top: -113px;


    &__container {
        position: relative;
    }


    &__inner {
        position: relative;
        box-sizing: border-box;
        display: flex;
        flex-flow: column;
        margin: 0 auto;
        max-width: calc(#{$base-maxwidth} + #{$grid-columngap});
        overflow: hidden;
        padding: 0 ($grid-columngap / 2) 1.875em;
        z-index: 2;
        width: 100%;
        color: white;
        min-height: calc(100vh - 164px);
        @media (max-width: $media-max-XL) {
            min-height: calc(100vh - 113px);
            padding-left: $grid-outer-mobile-padding;
            padding-right: $grid-outer-mobile-padding;
        }

        &__date {
            padding: 0;
            font-size: 18px;
            margin-top: auto;
            margin-bottom: 10px;

            & > span {
                text-transform: uppercase;
                font-weight: 700;
                padding-right: 5px;
            }
        }

        &__title {
            margin-bottom: 140px;
        }
    }

    &__media {
        background-size: cover;
        bottom: 0;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
        overflow: hidden;
        margin-top: -113px;
        width: 100%;
        z-index: 0;

        &:after {
            background-color: #141414;
            bottom: 0;
            content: "";
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
            z-index: 1;
            opacity: 0.4;
        }
    }

    &__controls-container {
        position: relative;
        box-sizing: border-box;
        margin: 0 auto;
        max-width: calc(#{$base-maxwidth} + #{$grid-columngap});
        width: 100%;

        @media (max-width: $media-max-XL) {
            padding-left: $grid-outer-mobile-padding;
            padding-right: $grid-outer-mobile-padding;
        }
    }

    &__controls {
        position: absolute;
        bottom: 10vh;
        right: 0;
        display: flex;
        z-index: 2;
        @media (max-width: $media-max-XL) {
            right: 20px;
        }
        @media (max-width: 767px) {
            display: none;
        }

        & li {
            cursor: pointer;

        }

        & li svg {
            fill: #264273;
            background-color: white;
            border: 1px solid #264273;
        }

        & li:hover svg {
            fill: white;
            background-color: #264273;
        }
    }

    & .tns-inner {
        padding-top: 113px !important;
        overflow: hidden;
    }

    & .tns-nav {
        z-index: 2;
    }

    & .tns-item {
        position: relative;
    }

    .tns-nav button {
        width: 57px;
        border-radius: 0;
    }
}
