/*
 * Steps to process fonts correctly:
 *
 * 1. Go on https://www.fontsquirrel.com/tools/webfont-generator
 * 2. Upload the `/src/fonts/generator_config.txt` config file
 * 3. Upload your fonts
 * 4. Install them into `/src/fonts/` following these guidelines
 *    https://github.com/7studio/bichoco-font-faces
 *
 * Only put here your critical fonts and let others into `fonts.async.css` (deprecated).
 *
 * Don't forget to define the `unicode-range` descriptor as much as possible
 * to optimise the fonts loading.
 * https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/unicode-range
 * https://github.com/ben-eb/postcss-unicode-characters
 */

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto/light.woff2?#iefix") format("woff2")
     , url("../fonts/Roboto/light.woff") format("woff");
  font-style: normal;
  font-weight: 300;
}
