$elem: sba-footer;

.#{$elem} {
    background-color: $color-blue;
    color: #fff;
    font-size: 1rem;

    &__inner {
        margin: 0 auto;
        max-width: $base-maxwidth;
        padding: 3.75em ($grid-columngap / 2) 3.125em; /* 60px 0 50px */

        &:not(:last-child) {
            border-bottom: 1px solid;
        }

        &--small {
            padding-bottom: $base-lineheight;
            padding-top: $base-lineheight;
        }
        @media (max-width: $media-max-XL) {
            padding: 3.75em $grid-outer-mobile-padding 3.125em;
        }
    }

    &__social {
        display: table;
        font-weight: 700;
        margin: 0 0 0 auto;

        &-icon {
            margin: 0 8px 0 10px;
            vertical-align: middle;
        }
    }

    &__navigation {
        line-height: 1.25em; /* 20px */
        overflow: hidden;

        &-section {
            display: block;
            margin: 0 0 1.25em; /* 0 0 20px */
            position: relative;
        }

        &-section::before {
            border-top: 1px solid;
            bottom: -.625em; /* -10px */
            color: $color-lightblue;
            content: "";
            left: 0;
            opacity: .35;
            position: absolute;
            right: 0;
        }

        &-subitem {
            margin: .625em 0 0;
        }

        &-link {
            color: $color-lightblue;
            display: inline-block;
            font-size: .75em; /* 12px */
            font-weight: 700;
        }
    }

    &__copy {
        font-size: .75em; /* 12px */
        font-weight: 700;
        opacity: .6;
    }

    @media (max-width: 575px) {
        &__inner {
            padding-bottom: $base-lineheight;
            padding-top: $base-lineheight;

            &--small {
                padding-bottom: ($base-lineheight / 1.5);
                padding-top: ($base-lineheight / 1.5);
            }
        }

        &__sitemap .sba-button {
            justify-content: center !important;
            width: 100% !important;
        }

        &__social {
            margin: 0;
        }
    }
}




