$elem: sba-news-content;

.#{$elem} {
    &__hero{
        &:after{
            //display: none;
        }
        .sba-hero__inner{
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            @media (max-width: $media-max-L) {
                flex-direction: column-reverse;
            }
        }
        .sba-hero__title{
            flex-basis:80%;
            padding-top:0;
            margin:0;
            padding-top: 4vw;
            @media (max-width: $media-max-L) {
                display: flex;
                align-items: flex-end;
                font-size: 1.4em;
            }
        }
        .sba-hero__date{
            color:white;
            padding-top: 4vw;
            @media (max-width: $media-max-L) {
                padding-top:8vw;
                margin-left:auto;
            }
              time{
                border-bottom:1px solid white;
                font-weight:200;
                font-size:22px;
                padding-bottom:12px;
              }
              span{
                font-size:90px;
                font-weight: bold;
                line-height: 1;
                display: block;
              }
        }
        .sba-hero__media{
            &:after{
                content:'';
                position:absolute;
                opacity: .5;
                background-image: linear-gradient(-180deg, rgba(35,93,167,0.00) 0%, #06205B 100%);
                width: 100%;
                height:100%;
                top:0;
                right:0;
                bottom:0;
                left:0;
            }
        }
    }
    font-size: 1rem;
    padding: 2.8125em 0 8.4375em; /* 45px 0 135px */

    &__inner {
        margin: 0 auto;
        max-width: $base-maxwidth;
        padding: 0 ($grid-columngap / 2);
    }


    &__banner {
        align-items: center;
        background-color: #fbfbfb;
        display: flex;
        flex-flow: row nowrap;
        margin: 0 0 1.875em; /* 0 0 30px */
        padding: 2.34375em 24px; /* 37.5px 24px */
        @media (max-width: $media-max-L) {
          flex-flow:row wrap;
          padding: 8px 24px; /* 37.5px 24px */
        }
        &-icon {
            margin: 0 30px 0 0;
             @media (max-width: $media-max-L) {
                margin:0 auto;
            }
        }

        &-name {
            font-size: 1.125em; /* 18px */
            font-weight: 700;
            line-height: 1.111em; /* 20px */
        }

        &-description {
            font-size: .875em; /* 14px */
            line-height: 1.429em; /* 20px */
            margin: .714em 0; /* 0 0 10px */
        }

        &-button {
            line-height: 0;
            display: flex;
             @media (max-width: $media-max-L) {
                justify-content: center;
            }
            li{
                margin-right: 12px;
            }
        }
    }
    &__main.sba-grid__item {
        flex-basis:70%;
        margin-right: 5%;
        @media (max-width: $media-max-L) {
            margin-right: 0;
        }
    }
    &__aside.sba-grid__item {
        flex-basis:25%;
    }
    &__author{
        display: flex;
        margin-top:64px;
        align-items: center;
        &-picture{
            border-radius: 50%;
            display: inline-block;
            overflow:hidden;
            margin-right: 32px;
            max-height: 50px;
            min-width: 50px;
        }
        &-name{
            line-height: 1;
            strong{
                display: block;
                line-height: 1.2;
            }
        }
    }
}
.sba-news-categories{
    padding-top: 16px;
    li{
        display: inline-block;
    }
    span{
        display: block;
        border:1px solid $gull-gray;
        border-radius:15px;
        padding:0 12px;
        color:$prussian-blue;
        font-size:12px;
        font-weight: bold;
    }
}
.sba-social-media{
    padding-bottom:32px;
    svg{
        stroke:transparent;
        height: 15px;
        width: 25px;
    }
    ul{
        display: flex;
    }
    a{
        display: flex;
        align-items: center;
        justify-content: center;
        width:50px;
        height: 50px;
        color:$prussian-blue;
        border-radius:50%;
        border:1.5px solid $alto-gray;
        margin-right:10px;
    }
}
