$elem: sba-wysiwyg;

.#{$elem} {
    font-size: 1rem;
    line-height: 1.875em;

    &::after {
        clear: both;
        content: "";
        display: block;
    }

    > *:first-child {
        margin-top: 0 !important;
    }

    > *:last-child {
        margin-bottom: 0 !important;
    }

    a:not([class]) {
        color: $color-blue;
    }

    a:not([class]):hover,
    a:not([class]):focus {
        color: $color-lightblue;
    }

    img {
        height: auto;
        max-width: 100%;
    }

    hr,
    p,
    ul,
    ol,
    table,
    blockquote {
        margin-top: 1.875em; /* 30px */
        margin-bottom: 1.875em; /* 30px */
    }

    h1 {
        color: #949594;
        font-size: 2.222em; /* 40px */
        font-weight: 300;
        line-height: 1.2375em; /* 49.5px */
        text-transform: uppercase;
    }

    h2 {
        color: #009ab7;
        font-size: 1.722em; /* 31px */
        font-weight: 300;
        line-height: 1.1976em; /* 37.125px */
        /*margin: 0 0 .9167em;  /* 0 0 22px */
    }

    h3 {
        color: #f66842;
        font-size: 1em; /* 18px */
        font-weight: 700;
        line-height: 1.375em; /* 27.5px */
        /*margin: 0 0 1em;  /* 0 0 22px */
    }

    h1 b,
    h1 strong,
    h2 b,
    h2 strong {
        font-weight: 700 !important;
    }

    h1 a,
    h2 a,
    h3 a {
        color: inherit !important;
    }

    ul,
    ol {
        overflow: hidden;
        padding: 0 0 0 40px;
    }

    li ul,
    li ol {
        margin-bottom: .9375em; /* 15px */
        margin-top: .9375em; /* 15px */
        overflow: visible;
    }

    li {
        margin: .46875em 0; /* 7.5px 0 */
    }

    ol {
        list-style-type: decimal;
    }

    ul {
        list-style-image: url(../../media/images/wysiwyg/img-disc.svg);
    }

    ul ul,
    ol ul {
        list-style-image: url(../../media/images/wysiwyg/img-circle.svg);
    }

    ol ol ul,
    ol ul ul,
    ul ol ul,
    ul ul ul {
        list-style-image: url(../../media/images/wysiwyg/img-square.svg);
    }

    table {
        border-collapse: separate;
        max-width: 100%;
        table-layout: fixed;
        width: 100% !important;
    }

    td,
    th {
        background-clip: padding-box;
        padding: 1.375em 20px; /* 22px 20px */
        width: auto !important;
    }

    th {
        font-weight: bold;
    }

    thead th,
    thead td {
        background-color: #009ab7;
        color: #fff;
        font-weight: 700;
        padding-bottom: .6111em; /* 11px */
        padding-top: .6111em; /* 11px */
    }

    table:not([cellspacing]) {
        border-collapse: collapse;
        border: 1px solid #d3d2c6;
    }

    table:not([cellspacing]) th,
    table:not([cellspacing]) td {
        border: 1px solid #d3d2c6;
    }

    .wp-caption {
        text-align: right;
        max-width: 100%;
        padding: 0;
        /*overflow: hidden;*/
    }

    .wp-caption-text,
    .wp-caption-dd {
        font-size: .618em;
        font-style: italic;
        width: 100%;
    }

    .alignleft,
    .alignright {
        margin: 0 0 5px;
        max-width: calc(50% - 10px) !important;
    }

    .alignleft {
        float: left;
        margin-right: 20px;
    }

    .alignright {
        float: right;
        margin-left: 20px;
    }

    .aligncenter:not(table) {
        display: block;
        margin: 1.375em auto;
    }

    .alignnone {
        margin: 1.375em 0;
    }

    .alignleft:first-child:not(:only-of-type) {
        margin-right: 10px;
    }

    .alignleft:first-child:not(:only-of-type) + .alignright {
        margin-left: 10px;
    }


}
