.sba-body {
    font-family: $base-fontfamily;
    color: $prussian-blue;
}

.sba-t1 {
    font-size: 120px;
    font-weight: 700;
    line-height: 120px;
}

.sba-t2 {
    font-size: 90px;
    font-weight: 700;
    line-height: 108px;
}

.sba-t3 {
    font-size: 60px;
    font-weight: 700;
    line-height: 71px;
}


.sba-text {
    font-size: 16px;
    line-height: 30px;
}

.sba-text-small {
    font-size: 14px;
    line-height: 18px;
}

@media screen and (max-width: 767px) {
    .sba-t1 {
        font-size: 53px;
        line-height: 62px;
    }
    .sba-t2 {
        font-size: 40px;
        line-height: 47px;
    }

    .sba-t3 {
        font-size: 35px;
        line-height: 42px;
    }
    .sba-text-small {
        line-height: 30px;
    }
}
