.sba-home-btn {
    background-color: transparent;
    font-size: 16px;
    font-weight: 400;
    color: white;
    width: 215px;
    padding: 15px;
    text-align: center;
    text-transform: uppercase;
    border-color: white;
    display: inline-block;
    transition: background-color 0.2s ease-in;

    &:hover {
        background-color: white;
        color: $prussian-blue;
        border-color: white;
    }
}

.sba-home-main {
    position: relative;
    margin-top: 100px;

    &__inner {
        max-width: calc(#{$base-maxwidth} + #{$grid-columngap});
        padding: 0 ($grid-columngap / 2) 1.875em;
        margin: 0 auto;
        @media (max-width: $media-max-XL) {
            padding-left: $grid-outer-mobile-padding;
            padding-right: $grid-outer-mobile-padding;
        }

        &__parallax-block {
            width: 100%;

            &__letter {
                position: absolute;
                left: 0;
                z-index: -3;
                top: 390px;

                > svg {
                    font-size: 650px;
                    font-weight: 700;
                    overflow: visible;
                }
            }

            &__overlay {
                position: absolute;
                background-color: #00205B;
                opacity: 0.9;
                top: 0;
                left: 0;
                z-index: -1;
                width: 100%;
                height: 500px;
                max-width: 60%;
            }

            &__content {
                color: white;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                z-index: 2;
                height: 500px;
                max-width: 40%;

                & p {
                    width: 100%;
                }

                & :first-child {
                    padding-top: 80px;
                    padding-bottom: 20px;
                }

                & a {
                    margin-top: auto;
                    margin-bottom: 40px;
                }

                &__text {
                    max-height: 6em;
                }
            }
        }
    }

    &--right {

        .sba-home-main__inner {
            &__parallax-block {
                &__letter {
                    position: absolute;
                    left: auto;
                    right: 0;
                    z-index: -3;

                    svg {
                        height: 650px;
                        width: 100%;
                        text-anchor: middle;
                    }
                }

                &__overlay {
                    position: absolute;
                    background-color: #00205B;
                    opacity: 0.9;
                    top: 0;
                    left: auto;
                    right: 0;
                    z-index: -1;
                    width: 100%;
                    height: 500px;
                    max-width: 60%;
                }

                &__content {
                    color: white;
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    z-index: 2;
                    min-height: 500px;
                    max-width: 40%;
                    margin-left: auto;
                    min-width: 50%;

                    & :first-child {
                        padding-top: 80px;
                        padding-bottom: 20px;
                    }

                    & button {
                        margin-top: auto;
                        margin-bottom: 40px;
                    }
                }
            }
        }
    }

    &--right__img {
        position: absolute;
        top: -30px;
        right: initial;
        left: 0;
        z-index: -2;
        height: 560px;
        max-width: 66.67%;

        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            font-family: "object-fit: cover";
        }
    }

    &__img {
        position: absolute;
        top: -30px;
        right: 0;
        z-index: -2;
        height: 560px;
        max-width: 66.67%;

        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            font-family: "object-fit: cover";
        }
    }
}

.sba-home-skywise {
    margin-top: 50px;
    background-color: #011133;
    position: relative;
    overflow: hidden;
    @media (max-width: 1010px) {
        display: flex;
        flex-direction: column-reverse;
    }

    &__inner {
        max-width: calc(#{$base-maxwidth} + #{$grid-columngap});
        padding: 0 ($grid-columngap / 2) 0;
        margin: 0 auto;
        display: flex;
        @media (max-width: $media-max-XL) {
            padding-left: $grid-outer-mobile-padding;
            padding-right: $grid-outer-mobile-padding;
        }

        &__content {
            width: 50%;
            padding-top: 110px;
            padding-right: 80px;
            padding-bottom: 70px;
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            @media (max-width: 1010px) {
                width: 100%;
            }

            &__title {
                @extend .sba-t3;
                color: white;
                margin-bottom: 20px;
                width: 100%;
            }

            &__paragraph {
                color: white;
                margin-bottom: 60px;
                width: 100%;
            }

            & button {
                margin-top: auto;
                width: 100%;
            }
        }
    }

    &__image {
        position: absolute;
        top: 0;
        left: 50%;
        width: 50%;
        height: 100%;
        @media (max-width: 1010px) {
            position: static;
            width: 100%;
        }

        img, video {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            font-family: "object-fit: cover";
            @media (max-width: 1010px) {
                position: static;
            }
        }
    }
}


.sba-home-store {
    position: relative;
    min-height: 550px;

    &__background {
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: -2;

        &:after {
            position: absolute;
            top: 0;
            left: 0;
            content: "";
            width: 100%;
            height: 100%;
            background-color: $prussian-blue;
            opacity: 0.30;
        }

        & img {
            width: 100%;
            height: 100%;
        }
    }

    &__inner {
        max-width: calc(#{$base-maxwidth} + #{$grid-columngap});
        padding: 0 ($grid-columngap / 2) 0;
        margin: 0 auto;
        @media (max-width: $media-max-XL) {
            padding-left: $grid-outer-mobile-padding;
            padding-right: $grid-outer-mobile-padding;
        }

        &__content {
            color: white;
            padding-top: 110px;
            padding-right: 80px;
            padding-bottom: 70px;

            &__title {
                @extend .sba-t3;
                color: white;
                margin-bottom: 20px;
                width: 100%;

                span {
                    color: $rock-blue;
                }
            }

            &__paragraph {
                margin-bottom: 60px;
                width: 100%;
            }
        }
    }
}

.sba-home-news {
    color: $prussian-blue;

    &__inner {
        max-width: calc(#{$base-maxwidth} + #{$grid-columngap});
        padding: 0 ($grid-columngap / 2) 0;
        margin: 0 auto;
        @media (max-width: $media-max-XL) {
            padding-left: $grid-outer-mobile-padding;
            padding-right: $grid-outer-mobile-padding;
        }

        &__content {
            color: $prussian-blue;
            padding-top: 25px;
            padding-right: 80px;
            padding-bottom: 0;

            &__title {
                @extend .sba-t3;
                margin-bottom: 20px;

                span {
                    color: $rock-blue;
                }
            }

            &__paragraph {
                margin-bottom: 40px;
            }
        }

        &__card {
            width: 100%;

            &__image {
                & img.img-covered {
                    width: 100%;
                    height: 200px;
                }
            }

            &__content {
                &__date {
                    margin-bottom: 5px;
                }

                &__title {
                    font-size: 20px;
                    font-weight: 700;
                    line-height: 24px;
                }
            }
        }
    }

    &__cta {
        margin: 30px 0;
        display: flex;
        justify-content: center;
        align-items: center;

        & button {
            color: $prussian-blue;
            background-color: white;
            font-weight: 400;
            border-color: $prussian-blue;
            width: 215px;
            height: 60px;
            text-transform: uppercase;
        }
    }
}

.sba-home-family {
    background-color: $prussian-blue;

    &__inner {
        max-width: calc(#{$base-maxwidth} + #{$grid-columngap});
        padding: 0 ($grid-columngap / 2) 0;
        margin: 0 auto;
        @media (max-width: $media-max-XL) {
            padding-left: $grid-outer-mobile-padding;
            padding-right: $grid-outer-mobile-padding;
        }

        &__content {
            color: $prussian-blue;
            padding-top: 80px;
            padding-bottom: 100px;

            &__title {
                color: white;
                @extend .sba-t3;
                margin-bottom: 60px;

                span {
                    color: $rock-blue;
                }
            }

            &__logo {
                display: flex;
                justify-content: center;
                align-items: center;

                & img {
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
}

.sba-ltr-fadein {
    transform: translateX(-50vw);
    opacity: 0;
}

@media screen and (max-width: 1152px) {

    .sba-home-main {
        &__inner__parallax-block {
            &__letter {
                display: none;
            }

            &__content {
                max-width: 80%;
            }

            &__overlay {
                max-width: 90%;
            }
        }

        &__img, &--right__img {
            max-width: 90%;
        }
    }
    .sba-home-main--right {
        .sba-home-main__inner__parallax-block {
            &__content {
                max-width: 80%;
            }

            &__overlay {
                max-width: 90%;
            }
        }

        &__img, &--right__img {
            max-width: 90%;
        }
    }
    .sba-home-skywise {
        &__inner__content {
            padding-right: $grid-outer-mobile-padding;

            & button {
                width: 90%;
                text-transform: capitalize;
            }
        }
    }
    .sba-home-store {
        min-height: auto;

        &__inner__content {
            padding-right: 0;
        }

        & button {
            max-width: 50%;
            text-transform: capitalize;
        }
    }
    .sba-home-news {
        &__inner__content {
            padding-right: 0;

            &__paragraph {
                margin-bottom: 20px;
            }
        }
    }
    .sba-home-family {

        &__inner__content {
            &__logo {
                width: 50%;
                min-height: 100px;
            }
        }
    }

}

@media screen and (max-width: 661px) {
    .sba-home-skywise__inner__content__paragraph {
        display: none;
    }
    .sba-home-skywise__inner__content a {
        width: 100%;
        padding: 15px 0;
    }

    .sba-home-family {
        background-color: white;

        &__inner__content {

            &__title {
                color: $prussian-blue;
                margin-bottom: 30px;
            }

            &__logo {
                width: calc(50% - 20px) !important;
                margin: 10px 0;
                background-color: $prussian-blue;
            }
        }
    }
}

.mb-30 {
    margin-bottom: 30px;
}
