html {
  font-size: $base-fontsize;

  -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
body {
  background-color: $base-bgcolor;
  color: $base-color;
  font: $base-font;

}

.c-default { color: #4a4a4a !important; }
.c-white { color: #fff !important; }
.c-blue { color: #00205b !important; }
.c-darkblue { color: #99bbcf !important; }



.sba-grid {
  align-items: flex-start;
  display: flex;
  flex-flow: row wrap;
  margin: -.9375rem -6px;
}
.sba-grid__item {
  box-sizing: border-box;
  flex: 0 0 auto;
  padding: .9375rem 6px;
  max-width: 100%;
}

.sba-grid--equalHeight {
  align-items: stretch !important;
}
.sba-grid--equalHeight > .sba-grid__item {
  display: flex;
}

.sba-grid--reverse { flex-direction: row-reverse; }


.sba-grid--align-justify { justify-content: space-around; }

.sba-grid--1col > .sba-grid__item { width: 100%; }
.sba-grid--2col > .sba-grid__item { width: 50%; }
.sba-grid--3col > .sba-grid__item { width: 33.333%; }
.sba-grid--4col > .sba-grid__item { width: 25%; }


@media (max-width: 1152px) {

  .sba-grid--1col\@XL > .sba-grid__item { width: 100%; }
  .sba-grid--2col\@XL > .sba-grid__item { width: 50%; }
  .sba-grid--3col\@XL > .sba-grid__item { width: 33.333%; }
  .sba-grid--4col\@XL > .sba-grid__item { width: 25%; }

  .sba-grid--noreverse\@XL { flex-direction: row; }

}
@media (max-width: 960px) {

  .sba-grid--2col\@L > .sba-grid__item { width: 50%; }

}
@media (max-width: 864px) {

  .sba-grid--1col\@L > .sba-grid__item { width: 100%; }
  .sba-grid--2col\@L > .sba-grid__item { width: 50%; }
  .sba-grid--3col\@L > .sba-grid__item { width: 33.333%; }
  .sba-grid--4col\@L > .sba-grid__item { width: 25%; }

  .sba-grid--noreverse\@L { flex-direction: row; }

}
@media (max-width: 575px) {

  .sba-grid--1col\@M > .sba-grid__item { width: 100%; }
  .sba-grid--2col\@M > .sba-grid__item { width: 50%; }
  .sba-grid--3col\@M > .sba-grid__item { width: 33.333%; }
  .sba-grid--4col\@M > .sba-grid__item { width: 25%; }

  .sba-grid--noreverse\@M { flex-direction: row; }

}
@media (max-width: 384px) {

  .sba-grid--1col\@S > .sba-grid__item { width: 100%; }
  .sba-grid--2col\@S > .sba-grid__item { width: 50%; }
  .sba-grid--3col\@S > .sba-grid__item { width: 33.333%; }
  .sba-grid--4col\@S > .sba-grid__item { width: 25%; }

  .sba-grid--noreverse\@S { flex-direction: row; }

}





.sba-page {
  font-size: 1rem;
  padding: 3.125em 0 8.4375em; /* 50px 0 135px */

  &__inner {
    margin: 0 auto;
    max-width: $base-maxwidth;
    padding: 0 ($grid-columngap / 2) 1.875em;
  }
}

