.sba-embed {
  display: block;
  margin: 0 auto;
  max-width: 100%;
  overflow: hidden;
  position: relative;
}
.sba-embed__ratio {
  display: block;
  padding-bottom: 100%;
  width: 100%;
}
.sba-embed__content {
	bottom: 0;
	height: 100%;
	left: 0;
	position: absolute;
	top: 0;
	width: 100%;
}
.sba-embed__content iframe {
	height: 100%;
	width: 100%;
}
