.tns-outer {
    padding: 0 !important;

    [hidden] {
        display: none !important;
    }

    [aria-controls], [data-action] {
        cursor: pointer;
    }
}

.tns-slider {
    -webkit-transition: all 0s;
    -moz-transition: all 0s;
    transition: all 0s;

    > .tns-item {
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }
}

.tns-horizontal {
    &.tns-subpixel {
        white-space: nowrap;

        > .tns-item {
            display: inline-block;
            vertical-align: top;
            white-space: normal;
        }
    }

    &.tns-no-subpixel {
        &:after {
            content: '';
            display: table;
            clear: both;
        }

        > .tns-item {
            float: left;
        }
    }

    &.tns-carousel.tns-no-subpixel > .tns-item {
        margin-right: -100%;
    }
}

.tns-no-calc {
    position: relative;
    left: 0;
}

.tns-gallery {
    position: relative;
    left: 0;
    min-height: 1px;

    > {
        .tns-item {
            position: absolute;
            left: -100%;
            -webkit-transition: transform 0s, opacity 0s;
            -moz-transition: transform 0s, opacity 0s;
            transition: transform 0s, opacity 0s;
        }

        .tns-slide-active {
            position: relative;
            left: auto !important;
        }

        .tns-moving {
            -webkit-transition: all 0.25s;
            -moz-transition: all 0.25s;
            transition: all 0.25s;
        }
    }
}

.tns-autowidth {
    display: inline-block;
}

.tns-lazy-img {
    -webkit-transition: opacity 0.6s;
    -moz-transition: opacity 0.6s;
    transition: opacity 0.6s;
    opacity: 0.6;

    &.tns-complete {
        opacity: 1;
    }
}

.tns-ah {
    -webkit-transition: height 0s;
    -moz-transition: height 0s;
    transition: height 0s;
}

.tns-ovh {
    overflow: hidden;
}

.tns-visually-hidden {
    position: absolute;
    left: -10000em;
}

.tns-transparent {
    opacity: 0;
    visibility: hidden;
}

.tns-fadeIn {
    opacity: 1;
    filter: alpha(opacity=100);
    z-index: 0;
}

.tns-normal, .tns-fadeOut {
    opacity: 0;
    filter: alpha(opacity=0);
    z-index: -1;
}

.tns-vpfix {
    white-space: nowrap;

    > {
        div, li {
            display: inline-block;
        }
    }
}

.tns-t-subp2 {
    margin: 0 auto;
    width: 310px;
    position: relative;
    height: 10px;
    overflow: hidden;
}

.tns-t-ct {
    width: 2333.3333333%;
    width: -webkit-calc(100% * 70 / 3);
    width: -moz-calc(100% * 70 / 3);
    width: calc(100% * 70 / 3);
    position: absolute;
    right: 0;

    &:after {
        content: '';
        display: table;
        clear: both;
    }

    > div {
        width: 1.4285714%;
        width: -webkit-calc(100% / 70);
        width: -moz-calc(100% / 70);
        width: calc(100% / 70);
        height: 10px;
        float: left;
    }
}

.tns-nav {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    margin: 10px 0;
    z-index: 1;
}

.tns-nav > [aria-controls] {
    width: 67px;
    height: 5px;
    background-color: #C3C2C2;
    padding: 0;
    margin: 0 5px;
    border-radius: 0;
    border: 0;
}

.tns-nav > .tns-nav-active {
    background: white;
}

.playbutton-wrapper {
    text-align: center;
}

.playbutton-wrapper button {
    height: 34px;
    padding: 0 10px;
    font-size: 14px;
    background: #fff;
    border-radius: 3px;
    border: 1px solid #ccc;
    cursor: pointer;
}

.wrapper {
    padding: 0 5em;
}

.suite-container .title,
.suite-container .subtitle,
.suite-container li {
    padding: 5px 10px;
}

.suite-container li {
    font-size: 12px;
    line-height: 1.4;
    background: #f5f5f5;
}
