$elem: sba-productBanner;

.#{$elem} {
  align-items: center;
  background: 50% / cover no-repeat;
  color: #fff;
  display: flex;
  font-size: 1rem;
  max-height: 540px;
  overflow: hidden;
  position: relative;
  z-index: 0;

  &::before {
    background-color: $color-blue;
    bottom: 0;
    content: "";
    left: 0;
    opacity: .34;
    position: absolute;
    right: 0;
    top: 0;
    z-index: -1;
  }

  &::after {
    box-sizing: border-box;
    content: '';
    flex: 0 0 auto;
    padding: 46.875% 0 0;

    width: 0;
  }

  &__inner {
    flex: 1 1 auto;
    margin: 0 auto;
    max-width: $base-maxwidth;
    padding: 4.6875em ($grid-columngap / 2) 9.375em; /* 75px n 150px */
    width: 100%;
      @media (max-width: $media-max-XL) {
          padding-left: $grid-outer-mobile-padding;
          padding-right: $grid-outer-mobile-padding;
      }
  }

  &__title {
    font-weight: 700;
    line-height: 1.25em; /* 75px */
    margin: 0 0 .5em; /* 0 0 30px */
  }

  &__subtitle {
    font-weight: 300;
    line-height: 1.25em; /* 37.5px */
    margin: 0 0 1.5em; /* 0 0 45px */
  }

  @media (max-width: 864px) {
    min-height: min-content;
  }

  @media (min-width: 1153px) {

    .#{$elem}__title { font-size: 3.75em; /* 60px */ }
    .#{$elem}__subtitle { font-size: 1.875em; /* 30px */ }

  }
  @media (max-width: 1152px) and (min-width: 385px) {

    .#{$elem}__title {
      font-size: calc(2.1875em + (3.75 - 2.1875) * ((100vw - 24.0625rem) / (72 - 24.0625)));
    }
    .#{$elem}__subtitle {
      font-size: calc(1em + (1.875 - 1) * ((100vw - 24.0625rem) / (72 - 24.0625)));
    }

  }
  @media (max-width: 384px) {

    .#{$elem}__title { font-size: 2.1875em; /* 35px */ }
    .#{$elem}__subtitle { font-size: 1em; /* 16px */ }

  }
}
