$elem: sba-cluster;

.#{$elem} {
    font-size: 1rem;
    padding: 1.5625em 0 8.4375em; /* 25px 0 135px */

    @media (max-width: $media-max-L) {
        padding: 1.5625em 0 5em; /* 25px 0 80px */
    }

    &__inner {
        margin: 0 auto;
        max-width: $base-maxwidth;
        padding: 0 ($grid-columngap / 2);

        @media (max-width: $media-max-XL) {
            padding: 0 $grid-outer-mobile-padding;
        }

    }

    &__nav {
        background: #fff;
        box-shadow: 0 -1px 0 0 #fff;
        color: $color-blue;
        overflow: hidden;
        position: sticky;
        top: 113px;
        z-index: 10;

        @media (max-width: $media-max-L) {
            display: none;
        }

        &::before {
            background-color: currentColor;
            bottom: 0;
            content: "";
            height: 1px;
            left: 0;
            position: absolute;
            right: 0;
            will-change: transform; /* fix Blink blur effect when sticky */
        }

        &-list {
            align-items: baseline;
            display: flex;
            flex-flow: row nowrap;
            margin: 0 -18px;
        }

        &-item {
            padding: .78125em 18px; /* 12.5px 18px */

            &.js-selected {
                //background: linear-gradient(to right, currentColor, currentColor) 50% 100% / calc(100% - 36px) 4px no-repeat;
            }
        }

        &-item--highliter {
            background-color: currentColor;
            bottom: 0;
            height: 4px;
            left: 0;
            will-change: transform;
            pointer-events: none;
            position: absolute;
            transform: translateX(-100%) scaleX(0);
            transform-origin: 0 0;
            transition: transform .25s ease-out;
            width: 100%;
        }

        &-link {
            /*background: blue;*/
            display: block;
            font-size: .875em; /* 14px */
            font-weight: 700;
            line-height: 1.25em; /* 20px */
        }
    }

    &__list {
        /*margin: -2.1875em 0; /* -35px 0 */
        padding: 1.5625em 0;
    }

    &__item {
        padding: 2.1875em 0; /* 35px 0 */
    }
}


.sba-domain {
    font-size: 1rem;
    overflow: hidden;

    &__media {

        img {
            max-width: 100%;
            width: auto;
        }
    }

    &__inner {
        margin: 0 18px;

        @media (max-width: 864px) {
            margin: 0;
        }
    }

    &__name {
        color: $color-blue;
        font-size: 3.75em; /* 60px */
        font-weight: 700;
        line-height: 1em;
        margin: 0 0 .25em;
    }

    &__description {
        line-height: 1.375em; /* 22px */
        margin: 0 0 .9375em; /* 0 0 15px */
    }

    &__sections {
        overflow: hidden;
    }

}

.sba-subsections {
    display: flex;
    flex-flow: row nowrap;
    font-size: 1rem;
    margin: 0 -18px;
    position: relative;
    transition: transform .25s ease-out;

    &__nav,
    &__item {
        box-sizing: border-box;
        flex: 0 0 auto;
        padding: 0 18px;
        width: 100%;
    }

    &__item {
        background-color: #fff;
        // position: relative;
        z-index: 0;
        min-height: 100%;
        position: absolute;
        left: 100%;

        & + & {
            left: 300%;
        }

    }

    &__nav-list {
        counter-reset: test;
        align-items: stretch;
        display: flex;
        flex-flow: row wrap;
        margin: 0 -15px;
    }

    &__nav-item {
        background: linear-gradient(to right, #9b9b9b, #9b9b9b) 0% 99.9999% / 100% 1px no-repeat;
        background-clip: content-box;
        box-sizing: border-box;
        flex: 0 0 auto;
        /*margin: 0 0 .9375em;*/
        padding: 0 15px;
        width: 50%;

        &:first-child:nth-last-child(1),
        &:first-child:nth-last-child(2),
        &:first-child:nth-last-child(2) ~ &,
        &:first-child:nth-last-child(3),
        &:first-child:nth-last-child(3) ~ &,
        &:first-child:nth-last-child(4),
        &:first-child:nth-last-child(4) ~ & {
            width: 100%;
        }

        &--ghost {
            background: 0;
        }

        @media (max-width: $media-max-M) {
            order: 0 !important;
            width: 100% !important;
        }
    }

    &__nav-link {
        counter-increment: test;
        align-items: center;
        box-sizing: border-box;
        color: $color-blue;
        display: flex;
        font-size: .875em; /* 14px */
        font-weight: 700;
        flex-flow: row nowrap;
        height: 100%;
        line-height: 1.25em; /* 17.5px */
        min-height: 4.642em;
        padding: 1.071em 0; /* 15px 0 */

        &::before {
            /*content: counter(test) ". ";*/
        }

        span,
        svg {
            will-change: transform;
            transition: transform .25s ease-out;
        }

        span {
            flex: 1 1 auto;
        }

        svg {
            flex: 0 0 auto;
            margin: 0 39px 0 24px;
        }

        &:hover,
        &:focus {
            background-color: $color-blue;
            color: #fff;

            span,
            svg {
                transform: translateX(18px);
            }
        }
    }

    &__nav--1col .sba-subsections__nav-item {
        width: 100%;
    }
}

.sba-subsection {
    font-size: 1rem;

    &__list {
        align-items: stretch;
        display: flex;
        flex-flow: row wrap;
        margin: 0 -15px;
    }

    &__name {
        background: linear-gradient(to right, #9b9b9b, #9b9b9b) 50% 100% / 100% 1px no-repeat;
        align-items: center;
        color: $color-blue;
        cursor: pointer;
        display: flex;
        width: 100%;
        font-size: 21px;
        height: 3.08em; /* IE11 fix: https://github.com/philipwalton/flexbugs/issues/231 */
        line-height: 1.25em;
        min-height: 3.09467em;

        svg {
            margin: 0 18px 0 0;
            transform: scaleX(-1);
        }
    }

    &__item {
        background: linear-gradient(to right, #9b9b9b, #9b9b9b) 50% 100% / calc(100% - 30px) 1px no-repeat;
        box-sizing: border-box;
        flex: 0 0 auto;
        padding: 0 15px;
        width: 50%;

        &--ghost {
            background: 0;
        }

        @media (max-width: 575px) {
            order: 0 !important;
            width: 100% !important;
        }
    }

    &__link {
        align-items: center;
        box-sizing: border-box;
        color: $color-blue;
        display: flex;
        font-size: .875em; /* 14px */
        font-weight: 700;
        flex-flow: row nowrap;
        height: 100%;
        line-height: 1.25em; /* 17.5px */
        min-height: 4.642em;
        padding: 1.071em 0; /* 15px 0 */
    }

    &__list--1col .sba-subsection__item {
        width: 100%;
    }
}
