$elem: sba-breadcrumb;

.#{$elem} {
    color: #fff;
    font-size: 1rem;

    position: sticky;
    top: 75px;
    z-index: 11;

    &::before {
      background-color: $color-blue;
      bottom: 0;
      box-shadow: 0 -1px 0 0 $color-blue;
      content: "";
      left: 0;
      opacity: 0;
      position: absolute;
      right: 0;
      top: 0;
      transition: opacity 250ms ease-out;
      z-index: -1;
    }

    &__inner {
        align-items: baseline;
        display: flex;
        flex-flow: row nowrap;
        margin: 0 auto;
        max-width: $base-maxwidth;
        //justify-content: flex-end;
        overflow-x: auto;
        padding: .46875em ($grid-columngap / 2) .9375em; /* 7.5px n 15px */
        -webkit-overflow-scrolling: touch;
        &::-webkit-scrollbar {
          display: none;
        }
        @media (max-width: $media-max-M) {
          padding-right: $grid-outer-mobile-padding;
          padding-left: $grid-outer-mobile-padding;
        }
    }

    &__item {
        align-items: center;
        display: inline-flex;
        font-size: .8125em; /* 13px */
        line-height: 1.154em; /* 15px */
        white-space: nowrap;
        &:last-child{
          @media (max-width: $media-max-M) {
            padding-right:24px;
          }
        }
    }

    &__separator {
        display: inline-block;
        line-height: 0;
        margin: 0 $grid-columngap;
        text-align: center;
        width: .9375em; /* 15px */
    }
    .js-nohero ~  &:before,
    &.js-sticky::before {
      opacity: 1;
    }
    .js-nohero ~  &:after,
    &.js-sticky::after {
      content:'';
      display: block;
      position:absolute;
      top:0;
      bottom:0;
      right:0;
      width:24px;
      height:100%;
      pointer-events: none;
      background:linear-gradient(to left,$prussian-blue, rgba( $prussian-blue, 0 ))
    }
}
